// store/useUserStore.ts

import { create } from "zustand";
import { retrieveLaunchParams } from "@telegram-apps/sdk";
import photoUrl from "../assets/images/avatar.png";
import { User as AppUser } from "../types/User";
import tg from "../utils/tg"; // ,   

//   
interface UserState {
  user: AppUser | null;
  isLoading: boolean;
  error: string | null;
  initializeUser: () => Promise<void>;
}

//      
const defaultUser: AppUser = {
  id: 2281337,
  first_name: "Muamee",
  last_name: "4ever",
  username: "test_user",
  language_code: "ru",
  is_premium: false,
  photo_url: photoUrl, //    
};

//    initDataRaw    
const verifyInitData = async (initDataRaw: string): Promise<AppUser | null> => {
  try {
    const response = await fetch(
      process.env.REACT_APP_API_URL
        ? `${process.env.REACT_APP_API_URL}/verify-init-data`
        : "https://your-server.com/api/verify-init-data",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `tma ${initDataRaw}`,
        },
      }
    );

    if (!response.ok) {
      throw new Error("   init   .");
    }

    const data = await response.json();

    // ,        
    return data.user as AppUser;
  } catch (error) {
    console.error("  init :", error);
    return null;
  }
};

//   Zustand
export const useUserStore = create<UserState>((set) => ({
  user: null,
  isLoading: false,
  error: null,

  initializeUser: async () => {
    set({ isLoading: true, error: null });

    // ,   Telegram WebApp    initDataUnsafe  
    if (tg && tg.initDataUnsafe && tg.initDataUnsafe.user) {
      const tgUser = tg.initDataUnsafe.user;

      //   AppUser   Telegram
      const appUser: AppUser = {
        id: tgUser.id,
        first_name: tgUser.first_name,
        last_name: tgUser.last_name || "",
        username: tgUser.username || "",
        language_code: tgUser.language_code || "ru",
        is_premium: tgUser.is_premium || false,
        photo_url: tgUser.photo_url || photoUrl,
      };

      //  initDataRaw    
      const { initDataRaw } = retrieveLaunchParams();

      if (initDataRaw) {
        //  initDataRaw    
        const verifiedUser = await verifyInitData(initDataRaw);

        if (verifiedUser) {
          //    ,    
          set({ user: verifiedUser, isLoading: false });
          console.log(
            "     ."
          );
        } else {
          //    ,    initDataUnsafe
          console.warn(
            " init   ,    initDataUnsafe."
          );
          set({ user: appUser, isLoading: false });
        }
      } else {
        //  initDataRaw ,    initDataUnsafe
        console.warn(
          "initDataRaw ,    initDataUnsafe."
        );
        set({ user: appUser, isLoading: false });
      }
    } else {
      // Telegram WebApp     
      if (process.env.NODE_ENV === "development") {
        //      
        console.log("    ().");
        set({ user: defaultUser, isLoading: false });
      } else {
        //      Telegram WebApp 
        console.warn(
          "Telegram WebApp .    ."
        );
        set({ user: defaultUser, isLoading: false });
        //       
        // set({ error: "Telegram WebApp ." });
      }
    }
  },
}));
