import React from "react";
import useCoinStore from "../../store/useCoinStore";
import styles from "./LevelingShop.module.scss";

const LevelingShop = () => {
  const { upgrades, purchaseUpgrade, coins, passiveIncomeRate } =
    useCoinStore();

  return (
    <div className={styles.levelingShop}>
      <h1>  </h1>
      <p>
           : {Math.round(passiveIncomeRate)} /
      </p>
      <p> : {Math.floor(coins)}</p>
      <div className={styles.upgrades}>
        {upgrades.map((upgrade) => (
          <div key={upgrade.id} className={styles.upgradeItem}>
            <h2>{upgrade.name}</h2>
            <p>
              : {upgrade.level} / {upgrade.maxLevel}
            </p>
            <p>: {upgrade.cost} </p>
            <p> : +{upgrade.rateIncreasePerLevel} /</p>
            <p>
               : +{Math.round(upgrade.totalRateIncrease)} /
            </p>
            <button
              onClick={() => purchaseUpgrade(upgrade.id)}
              disabled={
                coins < upgrade.cost || upgrade.level >= upgrade.maxLevel
              }
            >
              {upgrade.level >= upgrade.maxLevel
                ? " "
                : ""}
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default LevelingShop;
