import React, { useEffect } from "react";
import { Outlet } from "react-router-dom";
import FooterNav from "../components/FooterNav/FooterNav";
import Header from "../components/Header/Header"; //  Header
import useModalStore from "../store/useModalStore";
import Modal from "../components/UI/Modal/Modal"; //   Modal
import useCoinStore from "../store/useCoinStore"; //  useCoinStore
import { useUserStore } from "../store/useUserStore"; //    
import "./Layout.scss";

const Layout = () => {
  const { isModalOpen, modalContent, closeModal } = useModalStore();
  const {
    startPassiveIncome,
    stopPassiveIncome,
    startEnergyRecovery,
    stopEnergyRecovery,
    calculateOfflineIncome,
    setLastActiveTime,
    offlineIncome,
    setOfflineIncome,
    coins,
    incrementCoins,
  } = useCoinStore();

  const { initializeUser } = useUserStore(); //    

  useEffect(() => {
    initializeUser(); //     
  }, [initializeUser]);

  useEffect(() => {
    calculateOfflineIncome();
    setLastActiveTime(Date.now());

    startPassiveIncome();
    startEnergyRecovery();

    const handleBeforeUnload = () => {
      setLastActiveTime(Date.now());
    };

    window.addEventListener("beforeunload", handleBeforeUnload);

    return () => {
      stopPassiveIncome();
      stopEnergyRecovery();
      setLastActiveTime(Date.now());
      window.removeEventListener("beforeunload", handleBeforeUnload);
    };
  }, [
    startPassiveIncome,
    startEnergyRecovery,
    stopPassiveIncome,
    stopEnergyRecovery,
    calculateOfflineIncome,
    setLastActiveTime,
  ]);

  useEffect(() => {
    if (offlineIncome > 0) {
      //      store
      useModalStore.getState().openModal(
        <div>
          <h2 style={{ color: "#fff", marginBottom: "20px" }}> !</h2>
          <p style={{ color: "#fff", marginBottom: "20px" }}>
               ,   {Math.floor(offlineIncome)} .
          </p>
          <button
            style={{ background: "#fff", color: "black" }}
            onClick={() => {
              useModalStore.getState().closeModal();
              setOfflineIncome(0);
            }}
          >
             
          </button>
        </div>
      );
    }
  }, [offlineIncome, setOfflineIncome]);

  return (
    <div className="layout">
      <Header /> {/*  Header */}
      <main>
        <Outlet />
      </main>
      <FooterNav />
      {/*   Modal */}
      <Modal isOpen={isModalOpen} onClose={closeModal}>
        {modalContent}
      </Modal>
    </div>
  );
};

export default Layout;
